<template>
    <div class="paysult">
      <div class="success" v-if="status==0">
          <div class="tishi">
            <div class="iconfont icon-chenggong"></div>
            <span>支付成功</span>
          </div>
        <div class="caozuo">
          <router-link to="/">
            <van-button type="default" class="back">返回首页</van-button>
          </router-link>
          <router-link :to="{path:'/orderDetail',query:{orderId:query.order_id||query.orderId}}" >
            <van-button type="default" class="look">查看订单</van-button>
          </router-link>
        </div>
      </div>
      <div class="error" v-if="status==1">
        <div class="tishi">
          <div class="iconfont icon-shibai1"></div>
          <span>支付失败</span>
        </div>
        <van-button @click="$router.go(-1)" type="default" class="reback">重新支付</van-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "payresult",
      data(){
        const query = this.$route.query;
        console.log(query)
          return{
            query:query,
            status:query.result
          }
      }
    }
</script>

<style scoped lang="scss">
  .paysult{
    height: 100%;
    background: #E6E9ED;
    padding-top: 0.76rem;
  }
  .success,.error{
    height: 100%;
    .tishi{
      margin: 0 auto;
      width: 0.84rem;
      color: #3D7FEE;
      font-size: 0.19rem;
      text-align: center;
      .iconfont{
        font-size: 0.84rem;
      }
    }
    .caozuo{
      display: flex;
      width: 100%;
      height: 0.47rem;
      justify-content: space-around;
      .back{
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      }
      .look{
        background:linear-gradient(90deg,rgba(178,178,178,1),rgba(201,201,201,1));
      }
      button{
        width: 1.28rem;
        border-radius: 0.04rem;
        height: 0.47rem;
        font-size: 0.19rem;
        color: white;
      }
    }
  }
  .error{
    .iconfont{
      color: #CC1C1C;
    }
    span{
      color: #999999
    }
    .reback{
      width: 3.06rem;
      height: 0.47rem;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      border-radius:0.04rem;
      display: block;
      font-size: 0.19rem;
      color: white;
      margin: 0.89rem auto;
    }
  }
</style>



